content;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\r\n  position: absolute;\r\n  padding: 10px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: black;\r\n  color: white;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content {\r\n  padding: 16px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 {\r\n  margin: 0 0 10px 0;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n  padding: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content p {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4;\r\n  -webkit-box-orient: vertical;\r\n}\r\n\r\n\r\n/*\r\n  restaurant\r\n*/\r\n\r\n\r\n\r\n\r\n.restaurant {\r\n  margin-left: 30px;\r\n  width: 100%;\r\n  margin-top: 100px;\r\n  /* display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 18px 16px; */\r\n}\r\n\r\n.restaurant .detail-restaurant{\r\n  margin: 20px;\r\n  padding: 20px;\r\n}\r\n\r\n.restaurant .restaurant__name {\r\n  text-align: center;\r\n  margin: 10px 0;\r\n}\r\n\r\n.restaurant .restaurant__poster{\r\n  width: 100%;\r\n  max-width: 400px;\r\n  margin: auto;\r\n  display: block;\r\n}\r\n\r\n.restaurant .restaurant__info .restaurant__info__header {\r\n  margin: 20px 0;\r\n}\r\n\r\n.restaurant .restaurant__description {\r\n  margin: 20px 0 ;\r\n  border-top: black 1px solid;\r\n}\r\n\r\n.restaurant .restaurant__reviews {\r\n  border-top: black 1px solid;\r\n}\r\n\r\n.restaurant .restaurant__reviews .reviews{\r\n  margin: 10px;\r\n  padding: 10px 0;\r\n}\r\n\r\n.form-post-review {\r\n  /* width: max-content; */\r\n  display: block;\r\n  border-top: black 1px solid;\r\n  margin-left: 30px;\r\n  margin-bottom: 30px;\r\n  /* width: 100%;\r\n  max-width: 1000px; */\r\n}\r\n\r\n.form-post-review h3 {\r\n  margin: 10px 0;\r\n\r\n}\r\n\r\n.form-post-review .form-label {\r\n  font-weight: bold;\r\n  margin: 10px 10px;\r\n}\r\n\r\n.form-post-review .form-input {\r\n  width: 100%;\r\n  padding: 15px;\r\n  margin: 5px 20px 20px 0;\r\n  border: none;\r\n}\r\n\r\n\r\n\r\n/*\r\n  Like\r\n*/\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #ce1111;\r\n  \r\n  position: fixed;\r\n  bottom: 36px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n  \r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n.like:hover {\r\n  opacity: 0.8;\r\n}\r\n\r\n.btn {\r\n  border: none;\r\n  color: white;\r\n  padding: 14px 28px;\r\n  cursor: pointer;\r\n  background-color: red;\r\n}\r\n\r\n.btn:hover {background-color: orangered;}\r\n\r\n  /*\r\n    Footer\r\n  */\r\n\r\n  foot-bar {\r\n    width: 100%;\r\n    display: block;\r\n  } \r\n\r\n   \r\n  footer {\r\n    padding: 16px;\r\n    background-color: #db0000;\r\n}\r\n   \r\n  footer p {\r\n    text-align: center;\r\n    color: white;\r\n}\r\n   \r\n  footer p a {\r\n    color: skyblue;\r\n    text-decoration: none;\r\n}"],"sourceRoot":""}]);
